import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { HomeComponent } from './home/home.component';
import { OurMissionComponent } from './our-mission/our-mission.component';
import { OurVisionComponent } from './our-vision/our-vision.component';
import { GoalsForSunshineComponent } from './goals-for-sunshine/goals-for-sunshine.component';
import { ChairmanDeskComponent } from './chairman-desk/chairman-desk.component';
import { PrincipalDeskComponent } from './principal-desk/principal-desk.component';
import { ClassesComponent } from './classes/classes.component';
import { WhyChooseSunshineComponent } from './why-choose-sunshine/why-choose-sunshine.component';
import { AcademicSessionComponent } from './academic-session/academic-session.component';
import { AdmissionProcedureComponent } from './admission-procedure/admission-procedure.component';
import { BetweenUsPortalComponent } from './between-us-portal/between-us-portal.component';
import { TeacherComponent } from './teacher/teacher.component';
import { GalleryComponent } from './gallery/gallery.component';
import { EnquiryFormComponent } from './enquiry-form/enquiry-form.component';
import { CareerComponent } from './career/career.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { DirectorDeskComponent } from './director-desk/director-desk.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'our-mission', component: OurMissionComponent },
  { path: 'our-vision', component: OurVisionComponent },
  { path: 'goals-for-sunshine', component: GoalsForSunshineComponent },
  { path: 'chairman-desk', component: ChairmanDeskComponent },
  { path: 'principal-desk', component: PrincipalDeskComponent },
  { path: 'director-desk', component: DirectorDeskComponent },
  { path: 'classes', component: ClassesComponent },
  { path: 'why-choose-sunshine', component: WhyChooseSunshineComponent },
  { path: 'academic-session', component: AcademicSessionComponent },
  { path: 'admission-procedure', component: AdmissionProcedureComponent },
  { path: 'between-us-portal', component: BetweenUsPortalComponent },
  { path: 'teacher', component: TeacherComponent },
  { path: 'gallery', component: GalleryComponent },
  { path: 'enquiry-form', component: EnquiryFormComponent },
  { path: 'career', component: CareerComponent },
  { path: 'contact-us', component: ContactUsComponent },
  { path: '', redirectTo: 'home', pathMatch: 'full' },
];

@NgModule({
  declarations:[
    HomeComponent,
    OurMissionComponent,
    TeacherComponent,
    OurVisionComponent,
    GoalsForSunshineComponent,
    ChairmanDeskComponent,
    PrincipalDeskComponent,
    DirectorDeskComponent,
    ClassesComponent,
    WhyChooseSunshineComponent,
    AcademicSessionComponent,
    AdmissionProcedureComponent,
    BetweenUsPortalComponent,
    GalleryComponent,
    EnquiryFormComponent,
    CareerComponent,
    ContactUsComponent,
  ],
  imports: [RouterModule.forRoot(routes),NgbModule],
  exports: [RouterModule,HomeComponent],
  bootstrap: [HomeComponent]
})
export class AppRoutingModule { }
